*{
    padding: 0;
    margin: 0;
}
.siderow{
    display: flex;
    align-items: center;
    padding: 10px 20px;
   
}
.siderow_icon{
    color: rgb(0, 0, 0);
    font-size: large !important;
}
.siderow_title{
    flex: 1;
    margin-left: 10px;
    font-size: 12px;
    font-weight: 500;
    text-decoration: none;
    color: black;

}
.siderow:hover{
    cursor: pointer;
    background-color: rgb(196, 196, 196);
}
.siderow:hover>.siderow_icon{
    color: red;
}
.siderow:hover>.siderow_title{
    font-weight: bold;
}

.siderow.selected{
    background-color: rgb(196, 196, 196);
}
.siderow.selected>.siderow_icon{
    color: red;
}
.siderow.selected>.siderow_title{
    font-weight: bold;
}

