.videoresult_container {
    display: flex;

    height: 100%;
    margin-top: 20px;

}

.video-block {

    width: 100%;
    height: 500px;
    border: 2px solid black;
    border-radius: 10px;


}

.Home_sidebar {
    flex: 0.15;
}

.morevideo-container {
    flex: 0.3;
    /* border: 2px solid black; */
}

.video-container {

    flex: 0.6;
    margin-left: 10px;
    /* border: 2px solid black; */
}

.channel-detail {
    display: flex;
    justify-content: space-between;
    /* border: 2px solid black; */
    align-items: center;
    margin: 10px 0;
}
.logo-name{
    display: flex;
    align-items: center;
    width: 40%;
    /* border: 2px solid black; */
}
.logo-name p{
    margin-left: 15px;
}
.logo-name img{
    margin-left: 10px;
}

.channel-Logo {
    width: 40px;
    border-radius: 30px;

}

.video-detail {
    border: 1px solid black;
    font-weight: 500;
    height: 40px;
    border-radius: 15px;
    display: flex;
    align-items: center;
    margin-bottom: 15px;
}
.video-detail p{
    margin-left: 10px;
}

.comment-textarea {
    width: 100%;
    height: 70px;
    border-radius: 15px;
    padding-left: 5px;
    
}

.logo-name {
    display: flex;
}

.like-btn {
    background-color: white;
    border: none;
    margin-right: 10px;
}
.like-logo{
    color: rgb(255, 0, 43);
    font-size: 30px !important;

}

.morevideo-container {
    margin-left: 20px;
}
.comment-btn{
    width: 100%;
    height: 30px; 
    border-radius: 20px;
    margin-bottom: 15px;
}
.comment-block{
    border: 1px solid black;
    margin: 5px 0;
}
.extra-space{
    height: 10px;
}
.comment-block{
    border-radius: 10px;
    height:fit-content;
    padding-left: 7px;
}
.commnet-user{
    margin:5px 0 ;
    font-weight: 600;
}
.commnet-text{
    margin:5px 0 ;
}
.comment-section h3{
    margin-left: 5px;
}

@media screen and (max-width: 900px) {
    .morevideo-container {
      display: none;
    }
    .video-container{
        flex: 1;
        margin: 0 20px;
    }
    .video-block{
        height: 400px;
    }
  }