@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@200;300;400&display=swap');
*{
	margin:0px;
	padding:0px;
	box-sizing: border-box;
	font-family: 'Poppins', sans-serif;
}
.about-section{
	width: 100%;
	min-height: 100vh;
	background-color: rgb(255, 255, 255);
	display: flex;
}
.about-container{
	width: 80%;
	display: block;
	margin:auto;
	padding-top: 100px;
	/* border: 1px solid royalblue; */
}
.about-content-section{
	float: left;
	width: 55%;
}
.about-image-section{
	float: right;
	width: 40%;
}
.about-image-section img{
	width: 100%;
	height: auto;
}
.about-content-section .about-title{
	text-transform: uppercase;
	font-size: 28px;
    color: rgb(0, 0, 0);
}
.about-content-section .about-content h3{
	margin-top: 20px;
	color:#000000;
	font-size: 21px;
}
.about-content-section .about-content p{
	
    color: rgba(0, 0, 0, 0.8); 
    line-height: 28px;
    font-size: 18px;
    margin-bottom: 45px;
    margin-top: 20px;
}


.about-content-section .about-social{
	margin: 40px 40px;
}
.about-content-section .about-social i{
	color:#a52a2a;
	font-size: 30px;
	padding:0px 10px;
}
.about-content-section .about-social i:hover{
	color:#3d3d3d;
}
@media screen and (max-width: 768px){
	.about-container{
	width: 80%;
	display: block;
	margin:auto;
	padding-top:50px;
}
.about-content-section{
	float:none;
	width:100%;
	display: block;
	margin:auto;
}
.about-image-section{
	float:none;
	width:100%;
	
}
.about-image-section img{
	width: 100%;
	height: auto;
	display:block;
	margin:auto;
}
.about-content-section .about-title{
	text-align: center;
	font-size: 19px;
}
.about-content-section .about-content .about-button{
	text-align: center;
}
/* .about-content-section .about-content .about-button a{
	padding:9px 30px;
} */
.about-content-section .about-social{
	text-align: center;
}

}
.about-content span{
    color: #f9004d;
    font-size: 30px;
}


.about-button button{
    background:#f9004d ;
    color: rgb(0, 0, 0);
    text-decoration: none;
    border: 2px solid transparent;
    font-weight: bold;
    padding: 13px 30px;
    border-radius: 30px;
    transition: .4s;
    }
    
    .about-button button:hover{
        background: transparent;
        border: 2px solid #f9004d;
        cursor: pointer;
    }








