.trending_card{
    display: flex;
    width: 100%;
    margin:15px 0;
}
.trending_thumbnail{
    flex: 0.3;
    /* border: 2px solid rgb(248, 0, 0); */
    height: 100%;
    
}
.trending_thumbnail img{
    /* width: 300px; */
    width: 100%;

}
.trending_text img{
    width: 35px;
    height: 35px;
    border-radius: 20px;
    
}
.trending_text {
    flex: 0.8;
    /* border: 2px solid rgb(0, 248, 70); */
    padding-left: 15px;
    
}
.trending_card button{
    width: 20px;
    height: 10px;
    background-color: black;
}
@media screen and (max-width: 650px) {
    .trending_thumbnail{
        flex: 0.45;
    }
    .trending_text {
        flex: 0.5;
    }
}
