.cmt-card{
    width: 250px;
    border-radius: 10px;
    justify-content: space-between;
    margin: 10px;
    margin-left: 30px;
    margin-bottom: 40px;
}
.cmt-card>img{
    /* width: 100%; */
    height: 140px;
    width: 250px;
    border-radius: 10px 10px 0 0;
}
.cmt-title_card{
    display: flex;
    
}
.cmt-channel_avatar{
    display: flex;
   /* border: 1px solid green; */
flex: 0.2;
justify-content: center;
align-items: center;
}
.cmt-channel_avatar>img{
   
     width: 80%;
     border-radius: 20px;
}
.cmt-thumbnail{
    /* border: 1px solid rgb(128, 0, 0); */
    flex: 0.8;
    padding-top: 10px;
    padding-right: 20px;
    padding-left: 10px;
}
.cmt-thumbnail h3{
    font-size: medium;
}
.cmt-thumbnail p{
    font-size: small;
}
.cmt-Home_time,.cmt-Home_views{
    display: inline-block;

}
.cmt-Home_views{
    margin-left: 10px;

}
.cmt-thumbnail-title{
    margin-bottom: 10px;
}

