.navbar{
    display: flex;
    position: sticky;
    align-items: center;
    justify-content: space-between;
    height: 60px;
    z-index: 100;
    
/* border: 2px solid rgb(43, 255, 0); */
}
.first{
    display: flex;
    padding-left: 10px;
}

.second{
    display: flex;
    align-items: center;
    /* width: 45%; */
    border: 1px solid black;
    /* border-radius: 10px; */
    flex: 0.5;
    
}
.second input{
    outline: none;
    padding-left: 5px;
    
}
.third{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    margin-left: 10px;
}
.third p{
    margin-right: 10px;
}
.second>input{
    flex: 1;
   
    border: none;
    
}
.nav_icon{
    margin: 0 10px;
}
.second button{
    background-color: white;
    border: none;
}
.hide{
    display: none;
}
