.card{
    width: 250px;
    border-radius: 10px;
    justify-content: space-between;
    margin: 10px;
    margin-left: 30px;
    margin-bottom: 40px;
}
.card>img{
    /* width: 100%; */
    height: 140px;
    width: 250px;
    border-radius: 10px 10px 0 0;
}
.title_card{
    display: flex;
    
}
.channel_avatar{
    display: flex;
   /* border: 1px solid green; */
flex: 0.2;
justify-content: center;
align-items: center;
}
.channel_avatar>img{
   
     width: 80%;
     border-radius: 20px;
}
.thumbnail{
    /* border: 1px solid rgb(128, 0, 0); */
    flex: 0.8;
    padding-top: 10px;
    padding-right: 20px;
    padding-left: 10px;
}
.thumbnail h3{
    font-size: medium;
}
.thumbnail p{
    font-size: small;
}
.Home_time,.Home_views{
    display: inline-block;

}
.Home_views{
    margin-left: 10px;

}
.thumbnail-title{
    margin-bottom: 10px;
}
@media screen and (max-width: 900px) {
    .card>img{
        width: 100%;
    }
    .card{
        display: flex;
        flex-direction: column;
      width: 40%;
      border: 1px solid green;
    }
  }
@media screen and (max-width: 600px) {
    .card>img{
        width: 100%;
    }
    .card{

        display: flex;
        flex-direction: column;
      width: 80%;
      border: 1px solid green;
    }

  }
  @media only screen and (min-width: 900px) and (max-width: 1300px)  {
    
    .card>img{
        width: 100%;
    }
    .card{
        margin-left: 40px;
        display: flex;
        flex-direction: column;
      width: 40%;
      border: 1px solid green;
    }
  }
  @media only screen and (min-width: 1300px) and (max-width: 1500px)  {
    .card>img{
        width: 100%;
    }
    .card{

        display: flex;
        flex-direction: column;
      width: 25%;
      
      margin-left: 60px;
    }
  }
