.more-trending_card{
    display: flex;
    width: fit-content;
    margin:15px 0;
    /* border: 0.5px solid rgb(0, 0, 0);
    border-radius: 10px; */
}
.more-trending_thumbnail{
    flex: 0.45;
    /* border: 2px solid rgb(248, 0, 0); */
    height: 100%;
    
}
.more-trending_thumbnail img{
    /* width: 300px; */
    width: 100%;

}
.more-trending_text img{
    width: 35px;
    height: 35px;
    border-radius: 20px;
    
}
.more-trending_text {
    flex: 0.55;
    /* border: 2px solid rgb(0, 248, 70); */
    padding-left: 15px;
    
}
.more-trending_card button{
    width: 20px;
    height: 10px;
    background-color: black;
}
@media screen and (max-width: 650px) {
    .more-trending_thumbnail{
        flex: 0.45;
    }
    .more-trending_text {
        flex: 0.5;
    }
}
